* {
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  cursor: default;
}

body {
  margin: 0;
  background-color: #0B2434;
  font-family: 'Karla', sans-serif;
}

main {
  background-color: #F5F5F5;
  min-height: 400px;
  min-width: 800px;
  max-width: 1000px;
  border-radius: 5px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.container {
  height: 100vh;
  display: flex;
  justify-content:center;
  align-items: center;  
  padding: 0 2em;
}

.title {
  font-size: 40px;
  margin: 0;
}

.instructions {
  font-size: 1.2rem;
  font-weight: 500;
  color: #808080;
  margin-top: 0;
  text-align: center;
}

.dice-container {
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  grid-gap: 20px;
  margin-bottom: 40px;
}

.die-face {
  height: 50px;
  width: 50px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.die-num {
  font-size: 2rem;
  cursor: pointer;
}

.roll-dice {
  height: 50px;
  width: 150px;
  border: none;
  border-radius: 6px;
  background-color: #5035FF;
  color: white;
  font-size: 1.2rem;
  font-family: 'Karla', sans-serif;
  cursor: pointer;
}

.roll-dice:focus {
  outline: none;
}

.roll-dice:active {
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

.dice-count {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
.roll-count {
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  padding: 5px;
}

@media screen and (max-width: 850px) {
  main {
    min-width: fit-content ;
  }

  .dice-container {
    grid-gap: 15px; 
  }
}
